.products {
  position: relative;
  padding: 40px 20px;
}

.products__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.products__item {
  width: 45%;
  padding-bottom: 30px;
}

.products__cart-fast {
  display: none;
}

.products__heart {
  width: 30px;
  height: 30px;

  svg {
    width: 30px;
    height: 30px;
  }
}

.products__basket {
  svg {
    width: 30px;
    height: 30px;
  }

  span {
    display: none;
  }
}

.products__fav {
  position: absolute;
  z-index: 10;
  opacity: 0;
  justify-content: space-between;
  width: 100%;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  transition: 0.3s;
}

.products__move-top {

  position: absolute;
  right: 20px;
  bottom: 30px;
  width: 45px;
  height: 45px;
  padding: 0;
  background: no-repeat center #2d2d2d url("../../img/dropdown__arrow.png");
  transform: rotate(-180deg);

  span {
    display: none;
  }
}

@media (min-width: 768px) {
  .products__item {
    width: 30%;
  }
}

@media (min-width: 1280px) {
  .products {
    margin-top: 80px;
    padding: 20px 0;
  }

  // .products__item:hover > .products__fav {
  //   opacity: 1;
  //   padding-top: 20px;
  // }

  // .products__item:hover > .products__sale {
  //   display: none;
  // }

  .products__cart-fast {
    display: flex;
  }

  .products__move-top {
    display: none;
    bottom: 70px;
    width: 45px;
    height: 45px;
    right: -20px;

    &--active {
      display: block;
      position: sticky;
      margin-left: auto;
    }
  }

  .products__basket {
    display: none;

    &--active {
      display: block;
    }
  }
}
